* {
    margin: 0;
    padding: 0;
    font-family: "Poppins", 'Encode Sans Expanded', sans-serif;
    box-sizing: border-box;
}

::selection {
    background-color: #0ba376;
    color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #eee;
    font-family: poppins, Helvetica, sans-serif;
    font-weight: 600;
    color: #d5d5d5;
}

span {
    margin: 0 5px;
}